import React, { useEffect, useRef } from 'react';

import { NavLink } from 'react-router-dom';
import { Link } from 'react-scroll';
import { AuthButton } from '../../admin/components/AuthModal';

export const Main = () => {

    return (
        <ul className='navigation__list'>
            <li className='navigation__item'>
                <NavLink
                    to='/menu'
                >
                    tlapunk
                </NavLink>
            </li>
            <li className='navigation__item'>
                <Link
                    to='footer'
                    spy={true}
                    smooth={true}
                    duration={500}
                >
                    Kapcsolat
                </Link>
            </li>
            <li className='navigation__item'>
                <Link
                    to='find-us'
                    spy={true}
                    smooth={true}
                    duration={500}
                    offset={-140}

                >
                    Nyitva tarts
                </Link>
            </li>
            <li className='navigation__item'>
                <Link
                    to='chef'
                    spy={true}
                    smooth={true}
                    duration={500}
                    offset={-200}
                >
                    Rlunk
                </Link>
            </li>
        </ul>
    )
}

export const Menu = () => {
    return (
        <ul className='navigation__list'>
            <li className='navigation__item'>
                <NavLink
                    to='/'
                >
                    Foldal
                </NavLink>
            </li>
            <li className='navigation__item'>
                <Link
                    to='mains'
                    spy={true}
                    smooth={true}
                    duration={500}
                    offset={0}
                >
                    Burgerek
                </Link>
            </li>
            <li className='navigation__item'>
                <Link
                    to='drinks'
                    spy={true}
                    smooth={true}
                    duration={500}
                    offset={0}
                >
                    dtk
                </Link>

            </li>
            <li className='navigation__item'>
                <Link
                    to='tapas'
                    spy={true}
                    smooth={true}
                    duration={500}
                    offset={0}
                >
                    Tapas

                </Link>

            </li>
        </ul>
    )
}

export const AdminLinks = () => {
    return (
        <ul className='navigation__list'>

            <li className='navigation__item'>
                <NavLink
                    to='/admin'
                >
                    Admin
                </NavLink>
            </li>

            <li className='navigation__item'>
                <AuthButton />
            </li>

            <li className='navigation__item'>
                <NavLink
                    to='/'
                >
                    Foldal
                </NavLink>
            </li>


        </ul>
    )
}
